    margin-bottom: 16px;
  }
  h1,
  .h1 {
    font-size: 3.75rem;
    margin-top: 144px;
    margin-bottom: 32px;
  }
  h2,
  .h2 {
    font-size: 3.125rem;
    margin-top: 144px;
    margin-bottom: 16px;
  }
  h3,
  .h3 {
    font-size: 2.5rem;
    margin-top: 145px;
    margin-bottom: 16px;
  }
  h4,
  .h4 {
    font-size: 1.875rem;
  }
  h5,
  .h5 {
    font-size: 1.5625rem;
  }
  h6,
  .h6 {
    font-size: 1.25rem;
  }
  p.subtitle {
    font-size: 1.25rem;
  }
  figcaption {
    font-size: 0.8125rem;
  }
  blockquote {
    font-size: 2rem;
    padding-left: 324px;
    padding-top: 48px;
    padding-bottom: 20px;
    margin: 48px 0 28px;
    background: url(img/quote.svg) no-repeat left top;
    background-size: 179px;
  }
